"use client";

import DatePicker from 'react-datepicker';
import { format } from 'date-fns';
import { useState } from 'react';
import Image from 'next/image';
import styles from '../styles/srp.module.css'; // Update path if needed
import 'react-datepicker/dist/react-datepicker.css';

export default function DateSelector() {
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const handleChange = (dates) => {
    const [start, end] = dates;
    setStartDate(start);
    setEndDate(end);
  };

  return (
    <div className={`${styles["advance-search-option"]} ${styles["explore-check-in-out"]}`}>
      <Image src="/icons/calendar.svg" alt="calendar" width={20} height={20} />
      <DatePicker
        selectsRange
        startDate={startDate}
        endDate={endDate}
        onChange={handleChange}
        isClearable
        monthsShown={2}
        placeholderText="Select Check-In - Check-Out"
        dateFormat="dd/MM/yyyy"
        customInput={
          <input
            type="text"
            value={
              startDate && endDate
                ? `${format(startDate, 'dd/MM/yyyy')} - ${format(endDate, 'dd/MM/yyyy')}`
                : ''
            }
            readOnly
            placeholder="Select Check-In - Check-Out"
            className={styles["custom-input"]}
          />
        }
      />
    </div>
  );
}
